* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	text-decoration: none;
	user-select: none;
}
body{
	/*overflow-x: hidden;
	overflow-y: scroll;*/
	overflow: hidden;

	width:100%;
	height: 100%;
}
a:hover, a:visited, a:link, a:active {
	color:#6d10e7;
	text-decoration-style: none;
	outline: none;
}

.div-max-c{
	width: 1200px;
    padding-left:20px;
	margin: 20px auto 0 auto;
}
	@media (max-width:1200px){
		.div-max-c{
			width: 960px;
		}
	}
	@media (max-width:1200px){
		.div-max-c{
			width: 960px;
		}
	}
	@media (max-width:960px){
		.div-max-c{
			width: 100%;
		}
	}


.dis-blk{
	display: block;
}
.clearFix:after{
	content: '';
	display: block;
	clear: both;
	height: 0;
	visibility: hidden;
}
.div-cover{
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
    background-color: #000;
    opacity: 0.5;
}

.ul-nolist{
	list-style: none;
}
.h100{
	height: 100%;
}
.w100{
	width:100%;
}
.v-center{
	margin-left: auto;
	margin-right: auto;
}


